@charset "UTF-8";
/*Endast importer av partials*/
* {
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  min-height: 100vh;
  font-family: "Zen Maru Gothic", "Open Sans", sans-serif;
}

header {
  max-width: 1500px;
  width: 90%;
  margin: 0 auto;
  padding-top: 2em;
  background-color: white;
  color: white;
}
header h1 {
  font-family: "Indie Flower", Georgia, "Times New Roman", Times, serif;
  letter-spacing: 1px;
  color: #333;
}

main {
  max-width: 1500px;
  width: 90%;
  margin: 0 auto;
  padding: 3em 0;
  min-height: 300px;
  box-sizing: border-box;
  display: flex;
  gap: 5em;
}
main p {
  font-family: "Zen Maru Gothic", "Open Sans", sans-serif;
  font-size: 1em;
  line-height: 1.5em;
}
main section {
  width: 100%;
}

@media only screen and (max-width: 1400px) {
  header {
    max-width: 800px;
    width: 90%;
  }

  main {
    gap: 5em 0;
    flex-direction: column;
    max-width: 800px;
  }
  main p {
    font-family: "Zen Maru Gothic", "Open Sans", sans-serif;
    font-size: 1em;
    line-height: 1.5em;
  }
  main section {
    width: 100%;
  }
}
aside {
  height: 100px;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #ddd;
  text-align: right;
  padding: 0.5em;
}

.message, .success, .error {
  padding: 0.3em;
  font-weight: bold;
}

.error {
  background-color: #e25353;
}

.success {
  background-color: #78f17e;
}

.admin_collapsibleBtn {
  background-color: #333;
  color: white;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  margin-top: 1em;
  border-radius: 0.3em;
}
.admin_collapsibleBtn::after {
  content: "⮟";
  float: right;
  transform: scale(1.25);
}
.admin_collapsibleBtn:hover {
  background-color: black;
  color: white;
}

.collUp {
  border-top-right-radius: 0.2em;
  border-top-left-radius: 0.2em;
}
.collUp::after {
  content: "⮝";
  float: right;
  transform: scale(1.25);
}

.admin_collapsibleContent {
  display: none;
}

table {
  width: 100%;
  border-spacing: 0;
}
table th {
  text-align: left;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 1.2em;
  box-sizing: border-box;
  padding: 1em;
  background-color: #333;
  color: white;
}
table td {
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  padding: 1.5em;
  border-bottom: 2px solid #ddd;
}
table td button {
  width: 40px;
  height: 40px;
  border: none;
  background-color: white;
}
table td button:hover {
  transform: scale(0.95);
  cursor: pointer;
}
table td button img {
  width: 100%;
  height: 100%;
  border-radius: 0.2em;
}

@media all and (min-width: 760px) {
  th:nth-child(1),
th:nth-child(3),
th:nth-child(4),
th:nth-child(5) {
    width: 100px;
    text-align: center;
  }

  td:nth-child(1),
td:nth-child(3),
td:nth-child(4),
td:nth-child(5) {
    text-align: center;
  }
}
@media all and (max-width: 750px) {
  /* Force table to not be like tables anymore */
  table, thead, tbody, th, td, tr {
    display: block;
  }

  table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  table tr {
    border: 1px solid #ddd;
    margin-bottom: 1em;
  }
  table td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 40%;
    font-size: 0.95em;
  }
  table td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding: 1em;
    padding-right: 10px;
    white-space: nowrap;
  }
  table td:nth-of-type(1):before {
    content: "Kurskod:";
    width: 100px;
  }
  table td:nth-of-type(2):before {
    content: "Namn:";
    width: 100px;
  }
  table td:nth-of-type(3):before {
    content: "Progressation:";
    width: 100px;
  }
  table td:nth-of-type(4):before {
    content: "Kursinfo:";
    width: 100px;
  }
  table td:nth-of-type(5):before {
    content: "Hantera:";
    width: 100px;
  }
}
@media all and (max-width: 450px) {
  table td {
    padding-left: 50%;
  }

  fieldset {
    padding: 2em 1em;
  }

  form input[type=submit], .deleteBtn {
    width: 44%;
  }
}
form {
  width: 100%;
  margin: 0 auto;
  margin-top: 1em;
}
form fieldset {
  border: 2px solid #ddd;
  border-radius: 0.2em;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  width: 100%;
  padding: 2em 3em;
}
form legend {
  font-size: 1.2em;
  font-family: "Poiret One";
  font-weight: bold;
  padding: 0 0.2em;
}
form label {
  font-size: 1.1em;
  font-family: "Poiret One";
  font-weight: bold;
}
form input[type=text] {
  box-sizing: border-box;
  width: 100%;
  padding: 0.5em 0.3em;
  font-size: 0.9em;
  margin-bottom: 2em;
  margin-top: 0.1em;
}
form input[type=submit], form .submitBtn {
  background-color: #333;
  border: none;
  color: white;
  padding: 1em;
  border-radius: 0.2em;
  width: 30%;
  margin-right: 1em;
  cursor: pointer;
  float: right;
}
form input[type=submit]:hover, form .submitBtn:hover {
  transform: scale(0.95);
  background-color: black;
}
form ::placeholder {
  color: #999;
}
form .inputLocked {
  background-color: #ddd;
  border: 1px solid #333;
  border-radius: 0.2em;
  cursor: not-allowed;
}
form select {
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
}

.deleteBtn {
  background-color: #333;
  border: none;
  color: white;
  padding: 1em;
  border-radius: 0.2em;
  width: 30%;
  margin-right: 1em;
  cursor: pointer;
  background-color: white;
  color: black;
}
.deleteBtn:hover {
  transform: scale(0.95);
  background-color: black;
}
.deleteBtn:hover {
  background-color: #e25353;
  color: white;
  transform: scale(0.95);
}

.formBtns {
  background-color: #333;
  border: none;
  color: white;
  padding: 1em;
  border-radius: 0.2em;
  width: 30%;
  margin-right: 1em;
  cursor: pointer;
  background-color: white;
  color: #333;
  width: 100%;
  display: block;
}
.formBtns:hover {
  transform: scale(0.95);
  background-color: black;
}
.formBtns:hover {
  background-color: #e25353;
  font-weight: bold;
  color: white;
}